<template>
    <input
        class="zem-input"
        :class="inputClass"
        :value="value"
        :maxlength="maxLength"
        :disabled="disabled"
        :placeholder="placeholder"
        :type="type"
        :required="required"
        @input="onUpdateEvent($event.target.value)"
        @focus="onFocusEvent"
        @blur="onBlurEvent"
        @click="onClickEvent"
    />
</template>

<script>
export default {
  name: 'ZemInput',
  props: {
    value: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'text'
    },
    required: {
      type: Boolean,
      default: false
    },
    maxLength: {
      type: Number,
      default: 9999
    },
    isCompact: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    inputClass() {
      if (this.isCompact) {
        return 'zem-input--compact';
      }
      return '';
    }
  },
  methods: {
    onClickEvent() {
        this.$emit('click');
    },
    onUpdateEvent(value) {
        this.$emit('input', value);
    },
    onFocusEvent() {
        this.$emit('focus');
    },
    onBlurEvent() {
        this.$emit('blur');
    }
  }
};
</script>

<style lang="scss" scoped>
.zem-input {
    padding: 10px;
    width: 100%;
    height: 36px;
    border: 1px solid $color-alto-gray;
    box-sizing: border-box;
    border-radius: 4px;
    margin: 5px 0px;
    outline: none;
    color: $color-gull-gray;

    &:focus {
    border: 1px solid $color-accent;
    }

    &--compact {
      margin: 0;
      height: 25px;
    }
}
</style>