<template>
    <div class="table" :class="tableWidthClass">
      <div class="table__container">
        <Search v-if="isExchangeMode" />

        <div class="table__search-block" v-if="isPeopleCompaniesMode">
          <div class="table__search-column">
            <ZemTitle>{{ title }}</ZemTitle>
          </div>

          <div class="table__search-column">
            <Search2 />
          </div>

          <div class="table__search-column">
            <ZemButton :isPlusMode="true" @click="onPlusBtnClick">+</ZemButton>
          </div>
        </div>
        
        <table class="table__info">
          <tr>
            <th v-for="(item, ida) in head" :key="ida">{{ item.data }}</th>
          </tr>

          <tr v-for="(itemBody, idb) in body" :key="`${idb}-body`">
            <td v-for="(row, idc) in itemBody" :key="`${idc}-row`">
              <span v-if="row.type === 'checkbox'">
                <ZemCheckbox v-model="row.data"/>
              </span>
              <span v-if="row.type === 'text'">
                {{ row.data }}
              </span>
              <span v-if="row.type === 'image'">
                <img :src="row.data" class="table__image"/>
              </span>
            </td>
          </tr>

        </table>
        </div>
    </div>
</template>

<script>
import Search from './Search.vue';
import ZemCheckbox from './ui/Checkbox.vue';
import ZemTitle from './ui/Title.vue';
import ZemButton from './ui/Button.vue';
import Search2 from './Search2.vue';

export default {
  name: 'Table',

  props: {
    leftSidebar: {
      type: [Boolean],
      default: true
    },
    rightSidebar: {
      type: [Boolean],
      default: false
    },
    isExchangeMode: {
      type: [Boolean],
      default: false
    },
    isPeopleCompaniesMode: {
      type: [Boolean],
      default: false
    },
    tableData: {
      type: Object,
      default: () => ({})
    },
    title: {
      type: [String],
      default: ''
    },
  },

  components: {
    Search,
    ZemCheckbox,
    ZemTitle,
    ZemButton,
    Search2
  },

  data() {
    return {
      checkboxValue: true
    };
  },

  computed: {
    tableWidthClass() {
        if (this.leftSidebar && this.rightSidebar) {
          if (this.isPeopleCompaniesMode) {
            return 'table__width-wide-left-right';
          }
          return 'table__width-left-right';
        }
        if (this.leftSidebar && !this.rightSidebar) {
          if (this.isPeopleCompaniesMode) {
            return 'table__width-wide-left-only';
          }
          return 'table__width-left-only';
        }
        if (!this.leftSidebar && this.rightSidebar) {
          if (this.isPeopleCompaniesMode) {
            return 'table__width-wide-right-only';
          }
          return 'table__width-right-only';
        }
        return 'table';
    },
    head() {
      return this.tableData.head;
    },
    body() {
      return this.tableData.body;
    }
  },

  methods: {
    test() {
      console.log('checkboxValue =', this.checkboxValue)
    },
    onPlusBtnClick() {
      this.$emit('openModal');
    }
  }
};
</script>

<style lang="scss" scoped>
.table {
  display: block;
  width: calc(100vw - 4vw);
  height: 100vh;
  background: $color-titan-white;
  box-shadow: 2px 0px 8px rgba($color: $color-gray, $alpha: 0.12);
  &__image {
    width: 25px;
    height: 25px;
    border-radius: 2px;
  }

  &__search-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;
  }

  &__search-column {
    
  }

  &__width-left-only {
      width: calc(100vw - 4vw - 2px - 320px);
  }

  &__width-right-only {
      width: calc(100vw - 4vw - 2px - 560px);
  }

  &__width-left-right {
      width: calc(100vw - 4vw - 2px - 320px - 560px);
  }

  &__width-wide-left-only {
      width: calc(100vw - 4vw - 2px - 350px);
  }

  &__width-wide-right-only {
      width: calc(100vw - 4vw - 2px - 660px);
  }

  &__width-wide-left-right {
      width: calc(100vw - 4vw - 2px - 350px - 660px);
  }

  &__container {
    padding: 20px;
  }

  &__info {
    width: 100%;
    text-align: left;
    border-collapse: collapse;
    border-spacing: 0;

    th {
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 140%;
      color: $color-gull-gray;
    }

    td {
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 140%;
      color: $color-mine-shaft;
      padding: 9px 7px;
    }

    tr {
      cursor: pointer;

      &:hover {
        td {
          color: $color-accent;
        }
      }
    }

    tr:nth-child(even) {
      background-color: $color-catskill-white;
    }
  }
}
</style>