<template>
    <div class="zem-tabs">
        <div class="zem-tabs__tabs-container">
            <div  v-for="(item, index) in tabs"
                :key="index"
                class="zem-tabs__tab"
                :class="tabClass(item)"
                @click="onClickTab(item)">
                {{ item.name }}
            </div>
        </div>
        <div class="zem-tabs__body">
            <slot />
        </div>
    </div>
</template>

<script>
export default {
  name: 'ZemTabs',

  props: {
    tabs: {
      type: [Array],
      default: () => ([])
    },
    selectedTab: {
      type: [String],
      default: ''
    }
  },

  computed: {
  },

  methods: {
      onClickTab(item) {
        console.log(item);
        this.$emit('select', item.name);
      },
      tabClass(item) {
          if (this.selectedTab === item.name) {
              return 'zem-tabs__tab--selected';
          }
          return ''
      }
  }
};
</script>

<style lang="scss" scoped>
.zem-tabs {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;

    &__tabs-container {
        display: flex;

    }

    &__tab {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 100%;
        border: 1px solid $color-mystic;
        background: #D7EBEC;
        height: 30px;
        font-size: 14px;
        line-height: 16px;
        color: #3E3E3E;
        margin: 13px;
        cursor: pointer;

        &--selected {
            background-color: #fff;
            border: none;
            border-bottom: 2px solid #D7EBEC;
        }
    }

    &__body {
        width: 100%;
    }
}
</style>