<template>
<label class="zem-checkbox">
    <slot/>
    <input type="checkbox" 
        v-model="model"
        :value="inputValue"
        :required="required"
        :disabled="disabled"
    />
    <span class="checkmark"></span>
</label>
</template>

<script>
export default {
  name: 'ZemCheckbox',
  props: {
    value: {
      type: [String, Boolean, Array],
      default: ''
    },
    inputValue: {
      type: [String, Boolean],
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    required: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    model: {
      get() {
        return this.value;
      },
      set(value) {
        this.onUpdateEvent(value);
      },
    },
  },
  methods: {
    onUpdateEvent(value) {
        this.$emit('input', value);
    }
  }
};
</script>

<style lang="scss" scoped>
.zem-checkbox {
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 14px;
  line-height: 16px;
  color: $color-black;
  text-align: left;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.zem-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 14px;
  width: 14px;
  background-color: $color-white;
  border: 1px solid $color-alto-gray;
  border-radius: 2px;
}

.zem-checkbox:hover input ~ .checkmark {
  background-color: $color-white;
}

.zem-checkbox input:checked ~ .checkmark {
  background-color: $color-accent;
  border: 1px solid $color-accent;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.zem-checkbox input:checked ~ .checkmark:after {
  display: block;
}

.zem-checkbox .checkmark:after {
  left: 5px;
  top: 2px;
  width: 3px;
  height: 7px;
  border: solid $color-white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

</style>