<template>
    <div class="modal">
        <div class="modal__overlay"></div>
        <div class="modal__form" :class="modalFormClass">
            <slot />
            <div class="modal__close-btn" @click="closeModal">
                <img src="../../assets/icons/remove.svg" class="modal__close-icon" />
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Modal',

  props: {
    isCompactMode: {
      type: [Boolean],
      default: false
    },
    isAutoSize: {
      type: [Boolean],
      default: false
    }
  },

  components: {
  },

  data() {
    return {
        
    };
  },

  computed: {
    modalFormClass() {
      return this.isCompactMode ? 'modal__form--compact': this.isAutoSize ? 'modal__form--auto' : '';
    }
  },

  methods: {
      closeModal() {
          this.$emit('close');
      }
  }
};
</script>

<style lang="scss" scoped>
.modal {
    &__overlay {
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: 2;
        background: rgba($color: $color-tundora, $alpha: 0.85);
        backdrop-filter: blur(4px);
        position: fixed;
    }

    &__form {
        z-index: 3;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 60vw;
        // height: 60vh;
        background: $color-white;
        position: fixed;
        box-shadow: 0px 2.00573px 8.02292px rgba($color: $color-gray, $alpha: 0.12);
        border-radius: 4.01146px;
        padding: 24px;

        &--compact {
            width: 30vw;
        }

        &--auto {
            width: auto;
        }
    }

    &__close-btn {
        background: $color-swans-down;
        width: 1.6vw;
        height: 1.6vw;
        cursor: pointer;
        border-radius: 100px;
        position: absolute;
        top: -0.8vw;
        right: -0.8vw;
    }

    &__close-icon {
        width: 0.7vw;
        height: 0.7vw;
        text-align: center;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
    }
}
</style>