<template>
    <button class="zem-button" :class="btnClass" type="button" @click="onClickEvent">
        <slot />
    </button>
</template>

<script>
export default {
  name: 'ZemButton',

  props: {
    color: {
      type: [String],
      default: ''
    },
    isPlusMode: {
      type: [Boolean],
      default: false
    }
  },

  computed: {
    btnClass() {
      if (!this.isPlusMode) {
        switch (this.color) {
            case 'white':
                return 'zem-button--white';
            default:
                return ''
        }
      } else {
        return 'zem-button--plus';
      }
    }
  },
  methods: {
      onClickEvent() {
        this.$emit('click');
      }
  }
};
</script>

<style lang="scss" scoped>
.zem-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 16px;
    width: 100%;
    height: 40px;
    background: $color-accent;
    border: 1px solid $color-accent;
    border-radius: 4px;
    font-weight: 600;
    font-size: 14px;
    color: $color-white;
    cursor: pointer;
    transition-duration: 0.2s;

    &--plus {
      width: 40px;
      height: 40px;
    }

    &--white {
        background: $color-catskill-white;
        border: 1px solid $color-catskill-white;
        color: $color-gull-gray;

        &:hover {
          background: $color-riptide;
          color: $color-white;
        }
    }

    &:hover {
      background: $color-cornflower-blue;
      color: $color-white;
    }
}
</style>