<template>
    <div class="menu">
        <div class="menu__start-btn" @click="openUserMenu"></div>

        <div class="menu__user-block" v-if="isUserBlockVisible">
            <div class="menu__image-container">
                <img class="menu__image" src="../assets/icons/user.png" >
            </div>
            <div class="menu__data-block" @click="onOpenProfile">
               <div class="menu__user-name">
                   Иванов Сергей Петрович
               </div>
               <div class="menu__user-info">
                   Руководитель отдела продаж 000 "НКГ"
               </div>
               <div class="menu__user-info menu__user-info--email">
                   admin@erp.ru
               </div>
            </div>
            <div class="menu__logout-block">
            </div>
        </div>

        <div class="menu__item-container" @click="openExchange">
            <div class="menu__item-icon menu__item-icon--exchange"></div>
            <div class="menu__item-text">
                Биржа заявок
            </div>
        </div>

        <div class="menu__item-container" @click="openProfile">
            <div class="menu__item-icon menu__item-icon--exchange"></div>
            <div class="menu__item-text">
                Люди
            </div>
        </div>

        <div class="menu__settings-container" @click="openSettings">
            <div class="menu__item-icon menu__item-icon--settings"></div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Menu',

  props: {
      isUserMenuVisible: {
          type: Boolean,
          default: false
      }
  },

  mounted() {
     this.isUserBlockVisible = this.isUserMenuVisible;
  },

  watch: {
      isUserMenuVisible(value) {
          this.isUserBlockVisible = value;
      }
  },

  data() {
    return {
        isUserBlockVisible: false,
    };
  },

  methods: {
    openExchange() {
        this.$router.push('/exchange');
    },
    openProfile() {
        this.$router.push('/companies');
    },
    openSettings() {
        this.isUserBlockVisible = !this.isUserBlockVisible;
        this.$emit('openProfile', { state: true, name: 'Настройки'} );
    },
    openUserMenu() {
        if (!this.isUserBlockVisible) {
            this.$emit('openProfile', { state: false, name: 'Настройки'} );
        }
        this.isUserBlockVisible = !this.isUserBlockVisible;
        this.$emit('openUserMenu', this.isUserBlockVisible);
    },
    onOpenProfile() {
        this.$emit('openProfile', { state: true, name: 'Профиль'});
    }
  }
};
</script>

<style lang="scss" scoped>
.menu {
    background-color: $color-catskill-white;
    height: 100vh;
    width: 4vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    color: $color-mine-shaft;
    position: relative;

    &__user-block {
        position: fixed;
        display: flex;
        left: calc(4vw + 5px);
        top: 5px;
        background-color: $color-white;
        border: 1px solid $color-iron;
        width: 370px;
        height: 100px;
        border-radius: 2px;
        justify-content: flex-start;
        align-items: center;
        z-index: 3;
    }

    &__image-container {
        margin-left: 20px;
        margin-right: 12px;
    }

    &__image {
        width: 48px;
        border-radius: 100px;
    }

    &__data-block {
        text-align: left;
        margin-right: 50px;
        cursor: pointer;
    }

    &__user-name {
        font-size: 15px;
        line-height: 18px;
        color: $color-mine-shaft;
    }

    &__user-info {
        font-size: 12px;
        line-height: 130%;
        color: $color-gull-gray;

        &--email {
            color: $color-accent;
        }
    }

    &__logout-block {
        margin-right: 10px;
        background: url('../assets/icons/logout.svg');
        width: 32px;
        height: 32px;
        cursor: pointer;
    }

    &__start-btn {
        background-image: url('../assets/icons/start.svg');
        background-repeat: no-repeat;
        background-position: center;
        width: 30px;
        height: 31px;
        align-self: center;
        margin-top: 24px;
        cursor: pointer;
    }

    &__item-container {
        justify-content: center;
        align-content: center;
        align-items: center;
        align-self: center;
        margin: 20px;

        &:hover {
            .menu__item-icon--exchange {
                background: url('../assets/icons/exchange.svg');
            }
            .menu__item-text {
                color: $color-accent;
            }
        }
    }

    &__settings-container {
        position: absolute;
        bottom: 10px;
        left: 0;
        right: 0;
        margin: auto;
    }

    &__item-icon {
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;

        &--exchange {
            width: 32px;
            height: 32px;
            background: url('../assets/icons/exchange-o.svg');
        }

        &--settings {
            width: 20px;
            height: 20px;
            background: url('../assets/icons/settings.svg');
            margin-bottom: 30px;

            &:hover {
                opacity: 0.7;
            }
        }
    }

    &__item-text {
        font-size: 11px;
        line-height: 13px;
        text-align: center;
        margin: 0 7px 0 7px;
        cursor: pointer;
    }
}
</style>