<template>
    <div class="collapse-card">
        <div class="collapse-card__header" @click="onHeaderClick">
            {{ fields.title }}
            <span v-if="isBodyVisible" class="collapse-card__icon">-</span>
            <span v-else class="collapse-card__icon">+</span>
        </div>
        <div class="collapse-card__body"
            v-if="isBodyVisible">
            <div class="collapse-card__row" v-for="(item, idx) in getFields" :key="`${idx}-value`">
                <div class="collapse-card__column title">
                    {{ item.name}}
                </div>
                <div v-if="!item.isEdit" class="collapse-card__column collapse-card__column--second text" @click="onEditClick(idx)">
                    {{ item.value }}
                </div>
                <div class="collapse-card__input" v-else v-click-outside="vcoConfig">
                    <zem-input v-model="item.value" placeholder="item.name" isCompact />
                </div>
            </div>

            <div class="collapse-card__row" v-for="(newField, idb) in getNewFields" :key="`${idb}-new`">
                <div class="collapse-card__column colored colored--link">
                    + {{ newField.name }}
                </div>
                <!-- <div class="collapse-card__column collapse-card__column--second colored colored--link">
                    + {{ newField.value }}
                </div> -->
            </div>
        </div>
    </div>
</template>

<script>
import ZemInput from './ui/Input';

export default {
  name: 'CollapseCard',

  components: {
      ZemInput
  },

  props: {
    isOpenedDefault: {
      type: [Boolean],
      default: true
    },
    fields: {
        type: Object,
        default: () => ({})
    }
  },

  data() {
      return {
          currentFields: {},
          isBodyVisible: true,
          vcoConfig: {
            handler: this.onClickOutside,
            middleware: this.middleware,
            events: ['dblclick', 'click'],
            // Note: The default value is true, but in case you want to activate / deactivate
            //       this directive dynamically use this attribute.
            isActive: true,
            // Note: The default value is true. See "Detecting Iframe Clicks" section
            //       to understand why this behaviour is behind a flag.
            detectIFrame: true,
            // Note: The default value is false. Sets the capture option for EventTarget addEventListener method.
            //       Could be useful if some event's handler calls stopPropagation method preventing event bubbling.
            capture: false
         }
      }
  },

  computed: {
      getNewFields() {
          return this.currentFields.possibleToCreate;
      },
      getFields() {
          return this.currentFields.data;
      }
  },

  mounted() {
      this.currentFields = JSON.parse(JSON.stringify(this.fields));
      this.currentFields.data.forEach(f => {
          f.isEdit = false;
      })
      this.isBodyVisible = this.isOpenedDefault;
      this.$emit('collapse', this.isBodyVisible);
  },

  methods: {
      onHeaderClick() {
        this.isBodyVisible = !this.isBodyVisible;
        this.$emit('collapse', this.isBodyVisible);
      },
      onEditClick(index) {
        this.currentFields.data[index].isEdit = true;
        this.fixUpdateArr();
      },
      onClickOutside () {
        console.log('outside');
        this.closeAllInputs();
        this.fixUpdateArr();
      },
      // todo: temp solution;
      fixUpdateArr() {
          setTimeout(() => {
            this.currentFields.data.push({});
            this.currentFields.data.splice(-1);
        }, 1);
      },
      closeAllInputs() {
          this.currentFields.data.forEach(d => {
              d.isEdit = false;
          });
      },
      // Note: The middleware will be executed if the event was fired outside the element.
      //       It should have only sync functionality and it should return a boolean to
      //       define if the handler should be fire or not
      middleware (event) {
        return event.target.className !== 'modal'
      }
  }
};
</script>

<style lang="scss" scoped>
.collapse-card {
    border-radius: 4px;
    width: 100%;
    margin-top: 12px;

    &__icon {
        display: flex;
        justify-content: flex-end;
        flex: 1;
        margin-right: 15px;
        font-size: 22px;
        color: $color-accent;
    }

    &__input {
        display: flex;
        flex: 1;
    }

    &__header {
        background: $color-swans-down;
        height: 40px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        display: flex;
        align-items: center;
        color: $color-mine-shaft;
        padding-left: 12px;

        &:hover {
            background: $color-mystic;
            cursor: pointer;
        }
    }

    &__body {
        background: $color-athens-gray;
        display: block;
        padding: 12px;
    }

    &__column {
        flex: 1;

        &--second {
            flex: 1;
            cursor: pointer;
        }
    }

    &__row {
        display: flex;
        align-items: flex-start;
        align-self: flex-start;
        align-content: inherit;
        text-align: left;
        justify-content: flex-start;
        margin-bottom: 8px;
    }
}

.title {
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: $color-gull-gray;
}

.colored {
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: $color-accent;

    &--link {
        cursor: pointer;

        &:hover {
            text-decoration: underline;
        }
    }
}

.text {
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: $color-mine-shaft;
}
</style>