<template>
  <div class="zem-dropdown">
    <ZemDropdownList
      class="zem-dropdown__list"
      :dropdownValues="regionValues"
      :isOpen="isOpenRegion"
      @onOpen="openList"
    >
      Регион
    </ZemDropdownList>
    <ZemDropdownList
      class="zem-dropdown__list"
      :dropdownValues="statusValues"
      :isOpen="isOpenStatus"
      @onOpen="openList"
    >
      Статус на бирже
    </ZemDropdownList>
  </div>
</template>

<script>
import ZemDropdownList from "./ui/DropdownList";

export default {
  name: "DropdownFilterExchangeForm",
  data() {
    return {
      regionValues: [
        "Москва и МО",
        "Республика Дагестан",
        "Архангельская область",
        "Краснодарский край",
        "Удмуртская республика!",
        "Приволжский федеральный округ",
        "Ростовская область",
        "Чувашская Республика",
        "Оренбургская область",
        "Пензенская область",
        "Пермская область",
        "Свердловская область",
        "Республика Алтай",
        "Новосибирская область",
        "Амурская область",
        "Хабаровский край",
      ],
      statusValues: ["Активный", "Архивный", "Новый", "any else"],
      isOpenRegion: false,
      isOpenStatus: false,
    };
  },
  components: {
    ZemDropdownList,
  },
  methods: {
    openList(event) {
      switch (event.target.innerText.trim()) {
        case "Регион":
          this.isOpenRegion = !this.isOpenRegion;
          break;
        case "Статус на бирже":
          this.isOpenStatus = !this.isOpenStatus;
          break;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.zem-dropdown {
  display: flex;
  flex-direction: column;
}
</style>