<template>
  <div class="filter-exchange">
    <div class="filter-exchange__btn-block">
      <div class="filter-exchange__label">Фильтр</div>
      <div class="filter-exchange__reset-btn">Сбросить</div>
    </div>
    <div class="filter-exchange__label">Дата добавления заявки</div>
    <div class="filter-exchange__checkbox-block">
        <ZemCheckbox>Сегодня</ZemCheckbox>
        <ZemCheckbox>Последние 3 дня</ZemCheckbox>
        <ZemCheckbox>Последние 7 дней</ZemCheckbox>
        <ZemCheckbox>Последний месяц</ZemCheckbox>
        <div class="filter-exchange__period-block">
            <button class="filter-exchange__btn-period" @click="onSelectRange">
                <span class="filter-exchange__icon-period"></span>
                Выбрать период
            </button>

            <date-picker
                v-click-outside="vcoConfig"
                v-if="isDatePickerVisible"
                :inline="true"
                language="ru"
                apply-button-label="Применить"
                reset-button-label="Сбросить"
                :show-helper-buttons="false" 
                :switch-button-initial="true"
                :is-monday-first="true"
                @select-date="onDateApplied"
                @datepicker-closed="onDatepickerClosed"/>
        </div>
    </div>
    <DropdownFilterExchange class="filter-exchange__dropdown"/>
  </div>
</template>

<script>
import ZemCheckbox from "./ui/Checkbox";
import DropdownFilterExchange from "./DropdownFilterExchange";
import DatePicker from 'vue-time-date-range-picker/dist/vdprDatePicker';

export default {
  name: "FilterExchangeForm",

  components: {
    DropdownFilterExchange,
    ZemCheckbox,
    DatePicker
  },

  data() {
    return {
      region: '',
      name: '',
      phone: '',
      post: '',
      comment: '',
      isDatePickerVisible: false,
      vcoConfig: {
        handler: this.onClickOutside,
        middleware: this.middleware,
        events: ['dblclick', 'click'],
        // Note: The default value is true, but in case you want to activate / deactivate
        //       this directive dynamically use this attribute.
        isActive: true,
        // Note: The default value is true. See "Detecting Iframe Clicks" section
        //       to understand why this behaviour is behind a flag.
        detectIFrame: true,
        // Note: The default value is false. Sets the capture option for EventTarget addEventListener method.
        //       Could be useful if some event's handler calls stopPropagation method preventing event bubbling.
        capture: false
      }
    };
  },

  computed: {},

  methods: {
      slideEvent() {
        this.$emit("slide", this.isSideBarVisible);
      },
      addExchange() {
        alert("Add exchange");
      },
      getDateMonth(date) {
        return `${date.getDate()}/${date.getMonth()}`;
      },
      onDateApplied(date1, date2) {
          if (this.getDateMonth(date1) !== this.getDateMonth(date2)) {
              this.isDatePickerVisible = false;
              console.log(this.getDateMonth(date1))
              console.log(this.getDateMonth(date2))
          }
      },
      onDatepickerClosed(data) {
          console.log(data);
      },
      onSelectRange() {
          this.isDatePickerVisible = !this.isDatePickerVisible;
      },
      onClickOutside () {
        this.isDatePickerVisible = false;
      },
      // Note: The middleware will be executed if the event was fired outside the element.
      //       It should have only sync functionality and it should return a boolean to
      //       define if the handler should be fire or not
      middleware (event) {
        return event.target.className !== 'modal'
      }
  }
};
</script>

<style>
.vdpr-datepicker__calendar-input-wrapper {
    display: none;
}
.vdpr-datepicker {
    position: relative;
    left: 35px;
    top: -200px;
    z-index: 9;
}
.vdpr-datepicker__calendar-dialog--inline {
    position: absolute;
}
</style>

<style lang="scss" scoped>
.filter-exchange {
  display: block;
  padding: 24px;

  &__btn-block {
    display: flex;
  }
  &__label {
    font-size: 12px;
    line-height: 140%;
    color: $color-gull-gray;
    text-align: left;
    flex: 1;
  }
  &__reset-btn {
    font-size: 12px;
    line-height: 140%;
    color: $color-accent;
    text-align: right;
    flex: 1;
    margin-bottom: 16px;
    cursor: pointer;
    &:hover {
      text-decoration: underline;
    }
  }

  &__checkbox-block {
    margin-top: 16px;
  }

  &__period-block {
    display: flex;
  }

  &__btn-period {
    width: 135px;
    height: 37px;
    background: $color-catskill-white;
    border-radius: 4px;
    font-size: 12px;
    line-height: 140%;
    color: $color-gull-gray;
    border: 1px solid $color-catskill-white;
    cursor: pointer;
    &:hover {
      background: $color-iron;
      border: 1px solid $color-iron;
      color: $color-oslo-gray;
      opacity: 0.8;
    }
  }

  &__icon-period {
    width: 16px;
    height: 16px;
    display: inline-block;
    position: relative;
    top: 3px;
    left: -1px;
    background-image: url("../assets/icons/calendar.svg");
  }
  &__dropdown {
    margin-top: 16px;
  }
}
</style>