<template>
    <div class="sidebar-selector">
        <div class="sidebar-selector__header-container">
            <div class="sidebar-selector__title">
                Все контрагенты
            </div>
            <div class="sidebar-selector__value">
                {{ getQuantity | numberFormat}} шт.
            </div>
        </div>

        <div class="sidebar-selector__info-container">
            <div class="sidebar-selector__small-row">
                <div class="sidebar-selector__small-text">
                    Тип
                </div>
                <div class="sidebar-selector__small-text">
                    Количество
                </div>
            </div>

            <div class="sidebar-selector__row"
                v-for="(item, ida) in selectors" :key="ida"
                @click="onSelectItem(item)">
                <div class="sidebar-selector__option-text" :class="selectorClass(item)">
                    {{ item.name }}
                </div>
                <div class="sidebar-selector__option-text" :class="selectorClass(item)">
                    {{ item.value }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'SidebarSelector',
  props: {
    selectors: {
      type: Array,
      default: () => ([])
    },
    selected: {
        type: String,
        default: ''
    }
  },
  data() {
      return {
          selectedItem: null,
      }
  },
  mounted() {
      this.selectedItem = this.selectors.filter(i => i.name === this.selected)[0];
  },
  filters: {
    numberFormat (value) {
      return `${value.toLocaleString()}`
    }
  },
  computed: {
      getQuantity() {
          let quantity = 0;
          this.selectors.forEach((selector) => {
              quantity += Number(selector.value.replaceAll(/\s/g,''));
          });
          return quantity;
      }
  },
  methods: {
    onSelectItem(data) {
        this.selectedItem = data;
        this.$emit('select', data);
    },
    selectorClass(item) {
        if (item.name === this.selectedItem?.name) {
            return 'sidebar-selector__option-text--selected'
        }
        return '';
    }
  }
};
</script>

<style lang="scss" scoped>
.sidebar-selector {
    padding: 32px 24px;
    display: block;

    &__header-container {
        display: flex;
        flex-direction: row;
    }

    &__title {
        flex: 1;
        font-weight: 700;
        font-size: 17px;
        line-height: 20px;
        color: $color-mine-shaft;
    }

    &__value {
        flex: 1;
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        text-align: right;
        color: $color-gull-gray;
    }

    &__info-container {
        display: block;
        margin-top: 20px;
    }

    &__small-row {
        display: flex;
        justify-content: space-between;
        margin-bottom: 12px;
    }

    &__small-text {
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: $color-gull-gray;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        cursor: pointer;
        margin-bottom: 16px;

        &:hover {
            .sidebar-selector__option-text {
                color: $color-accent;
            }
        }
    }

    &__option-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        display: flex;
        align-items: center;
        color: $color-mine-shaft;

        &--selected {
            color: $color-accent;
        }
    }
    
}
</style>