<template>
    <div class="arrow-container" @click="onClicked" v-if="isArrowVisible">
        <img src="../../assets/icons/arrow.svg" class="arrow-container__image" :class="arrowClass" />
    </div>
</template>

<script>
export default {
  name: 'Arrow',
  props: {
    isInverted: {
      type: [Boolean],
      default: false
    },
    isNeedHideArrow: {
      type: [Boolean],
      default: false
    }
  },

  data() {
    return {
      isArrowClicked: false
    };
  },

  computed: {
    isArrowVisible() {
      if (this.isNeedHideArrow) {
        return !this.isArrowClicked;
      }
      return true;
    },
    arrowClass() {
        if (this.isInverted) {
            return !this.isArrowClicked ? 'arrow-container__image--reverse': 'arrow-container__image';
        }
        return this.isArrowClicked ? 'arrow-container__image--reverse': 'arrow-container__image';
    }
  },

  methods: {
      onClicked() {
          this.isArrowClicked = !this.isArrowClicked;
          this.$emit('clicked');
      }
  }
};
</script>

<style lang="scss" scoped>
.arrow-container {
    width: 32px;
    height: 32px;
    background-color: $color-accent;
    box-shadow: 0px 2px 6px rgba($color-accent, $alpha: 0.2);
    border-radius: 100px;
    display: flex;
    z-index: 2;

    &__image {
        width: 14px;
        height: 12px;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;

        &--reverse {
            transform: scaleX(-1);
        }
    }
}
</style>