<template>
  <div class="zem-dropdown">
    <div class="zem-dropdown-head" @click="onOpen">
      <slot />
      <img
        class="zem-dropdown-head__arrow"
        :class="arrowVisibilityClass"
        src="../../assets/icons/dropdown-arrow.svg"
        alt="drd-arrow"
      />
    </div>
    <div class="zem-dropdown-list" :class="listVisibilityClass">
      <div
        class="zem-dropdown-list__item"
        v-for="(item, idx) in dropdownValues"
        :key="idx"
      >
        <ZemCheckbox>{{ item }}</ZemCheckbox>
      </div>
    </div>
  </div>
</template>

<script>
import ZemCheckbox from "./Checkbox";
export default {
  name: "ZemDropdownList",
  components: {
    ZemCheckbox,
  },
  data() {
    return {};
  },
  props: {
    dropdownValues: {
      type: Array,
      required: true,
    },
    isOpen: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    listVisibilityClass() {
      return this.isOpen
        ? "zem-dropdown-list--show"
        : "zem-dropdown-list--hide";
    },
    arrowVisibilityClass() {
      return this.isOpen ? "zem-dropdown-head__arrow--reverse" : "";
    },
  },
  methods: {
    onOpen(event) {
      this.$emit("onOpen", event);
    },
  },
};
</script>

<style lang="scss" scoped>
.zem-dropdown-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 21px;
  margin: 8px 0 8px;
  // font-family: "Roboto";
  border-bottom: 1px solid rgba($color: $color-black, $alpha: 0.15);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  user-select: none;
  transition: all 0.5s;
  &:hover {
    border-bottom: 1px solid rgba($color: $color-black, $alpha: 0.6);
  }
  &__arrow {
    transition: all 0.25s;
    height: 7px;
    &--reverse {
      transform: rotate(180deg);
    }
  }
}
.zem-dropdown-list {
  width: 100%;
  overflow: auto;
  transition: max-height 0.25s;
  &--hide {
    max-height: 0px;
  }
  &--show {
    max-height: 175px;
  }
}
</style>