<template>
    <div class="sidebar-left" :class="sidebarVisibilityClass">
        <Arrow class="sidebar-left__arrow" @clicked="collapseSidebar" :isNeedHideArrow="isNeedHideArrow"/>
        
        <slot />
    </div>
</template>

<script>
import Arrow from './ui/Arrow';

export default {
  name: 'SidebarLeft',

  props: {
    isWide: {
      type: Boolean,
      default: false,
    },
    isHuge: {
      type: Boolean,
      default: false,
    },
    isNeedHideArrow: {
      type: Boolean,
      default: false,
    },
  },

  components: {
      Arrow
  },

  data() {
    return {
      isSideBarVisible: true
    };
  },

  computed: {
    sidebarVisibilityClass() {
      if (this.isWide) {
        return !this.isSideBarVisible ? 'sidebar-left-wide--hide': 'sidebar-left-wide--show';
      }
      if (this.isHuge) {
        return !this.isSideBarVisible ? 'sidebar-left-huge--hide': 'sidebar-left-huge--show';
      }
      return !this.isSideBarVisible ? 'sidebar-left--hide': 'sidebar-left--show';
    }
  },

  methods: {
      collapseSidebar() {
          this.isSideBarVisible = !this.isSideBarVisible;
          this.slideEvent();
      },
      slideEvent() {
        this.$emit('slide', this.isSideBarVisible);
      }
  }
};
</script>

<style lang="scss" scoped>
.sidebar-left {
    width: 320px;
    height: 100vh;
    background-color: $color-white;
    box-shadow: 2px 0px 8px rgba($color: $color-gray, $alpha: 0.12);
    display: block;
    position: relative;
    transition: all 0.5s;

    &--show {
        width: 320px;
    }

    &--hide {
        width: 1px;
    }

    &__arrow {
        position: absolute;
        right: -16px;
        top: calc(50vh - 16px);
        cursor: pointer;
    }
}

.sidebar-left-wide {
    width: 350px;

    &--show {
        width: 350px;
    }

    &--hide {
        width: 1px;
    }
}

.sidebar-left-huge {
    width: 550px;

    &--show {
        width: 550px;
    }

    &--hide {
        width: 1px;
    }
}
</style>