<template>
    <div class="sidebar-right" :class="sidebarVisibilityClass">
        <Arrow class="sidebar-right__arrow"
          :class="arrowClass"
          @clicked="collapseSidebar" :isInverted="true"/>
          
        <slot />
    </div>
</template>

<script>
import Arrow from './ui/Arrow';

export default {
  name: 'SidebarRight',

  props: {
    isWide: {
      type: Boolean,
      default: false,
    },
  },

  components: {
      Arrow
  },

  data() {
    return {
      isSideBarVisible: true
    };
  },

  computed: {
    sidebarVisibilityClass() {
      if (this.isWide) {
        return !this.isSideBarVisible ? 'sidebar-right-wide--hide': 'sidebar-right-wide--show';
      }
      return !this.isSideBarVisible ? 'sidebar-right--hide': 'sidebar-right--show';
    },
    arrowClass() {
      return !this.isSideBarVisible ? 'sidebar-right__arrow--hide': 'sidebar-right__arrow';  
    }
  },

  methods: {
      collapseSidebar() {
          this.isSideBarVisible = !this.isSideBarVisible;
          this.slideEvent();
      },
      slideEvent() {
        this.$emit('slide', this.isSideBarVisible);
      }
  }
};
</script>

<style lang="scss" scoped>
.sidebar-right {
    width: 560px;
    height: 100vh;
    background-color: $color-catskill-white;
    box-shadow: 2px 0px 8px rgba($color: $color-gray, $alpha: 0.12);
    display: block;
    position: relative;
    transition: all 0.5s;

    &--show {
        width: 560px;
    }

    &--hide {
        width: 1px;
    }

    &__arrow {
        position: absolute;
        left: -16px;
        top: calc(50vh - 16px);
        cursor: pointer;

        &--hide {
          left: -32px;
        }
    }
}

.sidebar-right-wide {
    width: 660px;

    &--show {
        width: 660px;
    }

    &--hide {
        width: 1px;
    }
}
</style>