<template>
    <textarea
        class="zem-textarea"
        :value="value"
        :maxlength="maxLength"
        :disabled="disabled"
        :placeholder="placeholder"
        :required="required"
        @input="onUpdateEvent($event.target.value)"
        @focus="onFocusEvent"
        @blur="onBlurEvent"
        @click="onClickEvent"
    />
</template>

<script>
export default {
  name: 'ZemTextarea',
  props: {
    value: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    required: {
      type: Boolean,
      default: false
    },
    maxLength: {
      type: Number,
      default: 9999
    }
  },
  methods: {
    onClickEvent() {
        this.$emit('click');
    },
    onUpdateEvent(value) {
        this.$emit('input', value);
    },
    onFocusEvent() {
        this.$emit('focus');
    },
    onBlurEvent() {
        this.$emit('blur');
    }
  }
};
</script>

<style lang="scss" scoped>
.zem-textarea {
    padding: 10px;
    width: 100%;
    height: 112px;
    border: 1px solid $color-alto-gray;
    box-sizing: border-box;
    border-radius: 4px;
    margin: 10px 0px;
    outline: none;
    color: $color-gull-gray;
    resize: none;
    overflow: hidden;

    &::placeholder {
      font-weight: 400;
      font-size: 14px;
    }

    &:focus {
      outline: 0;
      border: 1px solid $color-accent;
    }
}
</style>